<template>
    <div class="container">
        <div class="row">
          <div class="col s12 m8 offset-m2">
            <div class="login card-panel green white-text center">
               <h3 class="card-title center-align">Login</h3>
                <form>
        
                    <div class="input-field">
                    <i class="material-icons prefix">email</i>
                    <input type="email" id="email" v-model="email" class="validate" />
                    <label class="white-text" for="email">Email</label>
                    </div>
                
                
                    <div class="input-field">
                    <i class="material-icons prefix">lock</i>
                    <input type="password" id="password" v-model="password" class="validate" />
                    <label class="white-text" for="email">Password</label>
                    </div>
    
                <div class="center-align">
                    <button v-on:click.native="login" class="btn btn-large grey lighten-4 black-text">Login</button>
                </div>
                </form>
            
            </div>
        </div>
    </div>
</div>
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'login',
    data: function(){
        return {
            email: '',
            password: ''
        }
    },
    methods: {
        login: function(e){
            firebase
            .auth()
            .signInWithEmailAndPassword(this.email, this.password)
            .then(user => {
                alert(`You are logged in as ${user.user.email}`)
                this.$router.go({path: this.$router.path})
            },
              err => {
                  alert(err.message)
              }
              
              )

            e.preventDefault()
        }
    }

}
</script>

<style>


</style>



