<template>
    <div id="edit-client">
        <h3>Edit Client</h3>
         <div class="row">
            <form @submit.prevent="updateClient" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <span>Client ID:</span><input disabled type="text" v-model="Name_ID" required>
                         
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <span>Client Name:</span><input type="text" v-model="Name" required>
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <span>Client Description:</span><input type="text" v-model="Description" required>
                       
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <span>Client Date:</span><input type="text" v-model="myDate" required>
                        
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <span>Client Amount:</span><input type="text" v-model="Amount" required>
                        
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name:'edit-client',
    data(){
        return {
            Name_ID: null,
            Name: null,
            Description: null,
            myDate: null,
            Amount: null
            
        }
    },
     beforeRouteEnter (to, from, next) {
       db.collection('ClientData').where('Name_ID', 
       '==', to.params.Name_ID).get().then(querySnapshot => {
           querySnapshot.forEach(doc => {
               next(vm => {
                   vm.Name_ID = doc.data().Name_ID
                   vm.Name = doc.data().Name
                   vm.Description = doc.data().Description
                   vm.myDate = doc.data().myDate
                   vm.Amount = doc.data().Amount

               })
           })
       })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('ClientData').where('Name_ID', '==', this.$route.params.Name_ID).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.Name_ID = doc.data().Name_ID
                    this.Name = doc.data().Name
                    this.Description = doc.data().Description
                    this.myDate = doc.data().myDate
                    this.Amount = doc.data().Amount
                })
            })
        },
        updateClient(){
             db.collection('ClientData').where('Name_ID', '==', this.$route.params.Name_ID).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                   doc.ref.update({
                       Name_ID: this.Name_ID,
                       Name: this.Name,
                       Description: this.Description,
                       myDate: this.myDate,
                       Amount: this.Amount

                   })
                   .then(() => {
                       this.$router.push({name: 'view-client', params: {Name_ID: this.Name_ID}})
                   })
                })
            })

        }
     
   }
}
</script>


