<template>
    <div id="view-client">
       <ul class="collection with-header">
           <li class="collection-header"><h4>{{Name}}</h4></li>
           <li class="collection-item"><span class="view-title">Client ID: </span>{{ Name_ID}}</li>
           <li class="collection-item"><span class="view-title">Description:</span> {{ Description}}</li>
           <li class="collection-item"><span class="view-title">Date: </span>{{myDate}}</li>
           <li class="collection-item"><span class="view-title">Amount:</span> ${{ Amount}}</li>
       </ul>
       <router-link to="/" class="btn grey">Back</router-link>
       <button @click="deleteClient" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-client', params: {Name_ID: Name_ID}}" class="btn-floating btn-large red"><i class="fa fa-pencil"></i></router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name:'view-client',
    data(){
        return {
            Name_ID: null,
            Name: null,
            Description: null,
            myDate: null,
            Amount: null

            
        }
    },
    beforeRouteEnter (to, from, next) {
       db.collection('ClientData').where('Name_ID', 
       '==', to.params.Name_ID).get().then(querySnapshot => {
           querySnapshot.forEach(doc => {
               next(vm => {
                   vm.Name_ID = doc.data().Name_ID
                   vm.Name = doc.data().Name
                   vm.Description = doc.data().Description
                   vm.myDate = doc.data().myDate
                   vm.Amount = doc.data().Amount

               })
           })
       })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('ClientData').where('Name_ID', '==', this.$route.params.Name_ID).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.Name_ID = doc.data().Name_ID
                    this.Name = doc.data().Name
                    this.Description = doc.data().Description
                    this.myDate = doc.data().myDate
                    this.Amount = doc.data().Amount
                })
            })
        },
        deleteClient () {
            if(confirm('are you sure?')){
                db.collection('ClientData').where('Name_ID', '==', this.$route.params.Name_ID).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete();
                    this.$router.push('/')
                })
            })
         }
      }
   }
}
</script>

<style>

.collection.with-header{
   margin-top:70px;
   text-align:left;
   
}

.view-title{
    font-weight: bold;
}

</style>



