<template>
    <div id="new-client">
        <h3>New  Client</h3>
        <div class="row">
            <form @submit.prevent="saveClient" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="Name_ID" required>
                        <label>Client ID</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="Name" required>
                        <label>Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="Description" required>
                        <label>Description</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="myDate" required>
                        <label>Date</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="Amount" required>
                        <label>Amount</label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>

import db from './firebaseInit'

export default {
    name:'new-client',
    data(){
        return {
            Name_ID: null,
            Name: null,
            Description: null,
            myDate: null,
            Amount: null

            
        }
    },
    methods:{
        saveClient (){
            db.collection('ClientData').add({
                Name_ID: this.Name_ID,
                Name: this.Name,
                Description: this.Description,
                myDate: this.myDate,
                Amount: this.Amount
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }
    },
     
    
}
</script>


