<template>
    <nav>
        <div class="nav-wrapper green">
            <div class="nav-container">
              <router-link to="/" class="brand">Client Manager</router-link>   
              <ul class="right">
                 <li v-if="isLoggedIn"><span class="email black-text">Logged in: {{ currentUser}}</span></li>
                 <li v-if="isLoggedIn"><router-link to="/">Dashboard</router-link></li>
                 <li v-if="!isLoggedIn"><router-link  to="/login" id="login">Login</router-link></li>
                 <li v-if="!isLoggedIn"><router-link to="/register" id="login">Register</router-link></li>
                 <li v-if="isLoggedIn"><button v-on:click="logout" class="btn black">Logout</button></li>
              </ul>  
            </div>
        </div>
    </nav>
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'navbar',
    data(){
        return{
            isLoggedIn: false,
            currentUser: false
        }
    },
    created() {
       if(firebase.auth().currentUser){
           this.isLoggedIn = true;
           this.currentUser = firebase.auth().currentUser.email

       }
    },
    methods: {
        logout: function() {
            firebase
            .auth()
            .signOut()
            .then(() => {
                this.$router.go({path: this.$router.path})
            })
        }
    }
}
</script>


<style>
.nav-container{
    margin:0;
    padding-left:50px;
    padding-right:50px;
    text-align: center;
}

.search{
    width:30%;
}

.brand{
    font-size:2.5rem;
    
}

.email{
    padding-right:10px;
}

#login:hover{
cursor:pointer
}



@media screen and (max-width: 600px) {
    #login{
    color:black;
   
}
   
}



</style>



