<template>
    <div id="dashboard">
      <div class="search-container">
         <input type="text" v-model="search" class="searchbox" placeholder="Search...">
      </div>
        <h1>Client List By Most Recent Transaction</h1>
      <div class="cxDataTable">
         <table id="firstTable">
            <thead>
                <tr>
                <th @click="sortClient('Name_ID')">ID</th>
                <th @click="sortClient('Name')">Name</th>
                <th @click="sortClient('Description')">Description</th>
                <th @click="sortClient('myDate')">Date</th>
                <th @click="sortClient('Amount')">Amount</th>
                <th>View/Edit</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="ClientData in filterClientData" v-bind:key="ClientData.id">
                <td class="id-cell">{{ClientData.Name_ID}}</td>
                <td class="name-cell">{{ClientData.Name}}</td>
                <td class="desc-cell">{{ClientData.Description}}</td>
                <td class="date-cell">{{ClientData.myDate}}</td>
                <td class="amount-cell">${{ClientData.Amount}}</td>
                <td class="view-cell"><router-link class="secondary-content center-aligned" v-bind:to="{name:'view-client', params: {Name_ID: ClientData.Name_ID}}"><i class="fa fa-eye"></i></router-link></td>
                </tr>
            </tbody>
            </table>
        </div>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red"><i class="fa fa-plus"></i></router-link>
        </div>
    </div>
</template>

<script>

import db from './firebaseInit'
export default {
    name:'dashboard',
    data(){
        return {
            ClientData:[],
            search: '',
            sortBy: 'Name',
            sortDirection: 'ASC'

        }
    },
    created (){ 
      db.collection('ClientData').orderBy('myDate', 'desc').get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
              
              const data = {
                  'id': doc.id,
                  'Name_ID': doc.data().Name_ID,
                  'Name': doc.data().Name,
                  'Description': doc.data().Description,
                  'myDate':doc.data().myDate,
                  'Amount':doc.data().Amount

              }
              this.ClientData.push(data)
          })
      })
    
},
computed: {
    filterClientData: function(){
        return this.ClientData.filter((ClientData) => {
            return ClientData.Name.match(this.search) 
            || ClientData.Name_ID.match(this.search)
            || ClientData.Description.match(this.search)
            || ClientData.myDate.match(this.search)
            || ClientData.Amount.match(this.search)
            
        })
    },

  

     },

methods: {

     selectOrderBy: function (by) {
      if( by == this.sortBy ){
        if( this.sortDirection == 'ASC' ){
        this.sortDirection = 'DESC';
        }else{
        this.sortDirection = 'ASC';
        }

       if( by != this.sortBy ){
        this.sortDirection = 'ASC';
        this.sortBy = by;
       }
    

      
    }

   
   

},

    sortClient(){
    this.ClientData.sort( function( a, b ){
        if( this.sortDirection == 'ASC' ){
        return ( ( a.Name == b.Name )  ? 0 : ( ( a.Name > b.Name ) && ( a.Name == b.Name )  ? 1 : -1 ) );
        }

        if( this.sortDirection == 'DESC' ){
        return ( ( a.Name == b.Name )  ? 0 : ( ( a.Name < b.Name ) && ( a.Name == b.Name )   ? 1 : -1 ) );
        }
      }.bind(this));
    }

}
     }

  


    
</script>

<style>
.container {
    width: 100% !important;
}
h1{
    margin:0;
    padding:0;
    text-align: center;
    font-size: 2rem;
    margin-top: 20px;
}
.secondary-content{
    margin-right:25px;
}

.cxDataTable{
    margin-top:70px;
}

table {
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  width: 1150px;
  border-collapse: collapse;
  border: 3px solid rgb(68, 92, 75);
  margin-left:auto;
  margin-right:auto;
  margin-bottom:50px;
  padding-top:50px;
  padding-bottom:50px;
}

table th {
  text-transform: uppercase;
  text-align: left;
  background: rgb(159, 204, 132);
  color: #FFF;
  padding: 8px;
  min-width: 30px;
}

table td {
  text-align: left;
  padding: 8px;
  border-right: 2px solid #7D82A8;
}

table th:hover {
        background: #51e275;
      }
table td:last-child {
  border-right: none;
}
table tbody tr:nth-child(2n) td {
  background: rgb(159, 204, 132);
}

</style>



